(function() {
  'use strict';

  angular
    .module('webFront')
    .directive('acmeMalarkey', acmeMalarkey);

  /** @ngInject */
  function acmeMalarkey(malarkey) {
    var directive = {
      restrict: 'E',
      scope: {
        extraValues: '='
      },
      template: '&nbsp;',
      link: linkFunc,
      controller: MalarkeyController,
      controllerAs: 'vm'
    };

    return directive;

    function linkFunc(scope, el, attr, vm) {
      var watcher;
      var typist = malarkey(el[0], {
        typeSpeed: 40,
        deleteSpeed: 40,
        pauseDelay: 800,
        loop: true,
        postfix: ' '
      });

      el.addClass('acme-malarkey');

      angular.forEach(scope.extraValues, function(value) {
        typist.type(value).pause().delete();
      });

      watcher = scope.$watch('vm.contributors', function() {
        angular.forEach(vm.contributors, function(contributor) {
          typist.type(contributor.login).pause().delete();
        });
      });

      scope.$on('$destroy', function () {
        watcher();
      });
    }

    /** @ngInject */
    function MalarkeyController($log, githubContributor) {
      var vm = this;

      vm.contributors = [];

      activate();

      function activate() {
        return getContributors().then(function() {
          $log.info('Activated Contributors View');
        });
      }

      function getContributors() {
        return githubContributor.getContributors(10).then(function(data) {
          vm.contributors = data;

          return vm.contributors;
        });
      }
    }

  }

})();

(function() {
  'use strict';

  angular
    .module('webFront')
    .directive('acmeNavbar', acmeNavbar);

  /** @ngInject */
  function acmeNavbar() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/navbar/navbar.html',
      scope: {
          creationDate: '='
      },
      controller: NavbarController,
      controllerAs: 'vm',
      bindToController: true
    };

    return directive;

    /** @ngInject */
    function NavbarController(moment, $location) {
      var vm = this;
      vm.title = '龙虎榜数据统计'

      // "vm.creationDate" is available by directive option "bindToController: true"
      vm.relativeDate = moment(vm.creationDate).fromNow();
      vm.currentName = 'stock';
      $location.path( '/stock' );

      vm.click = function( name ){
        vm.currentName = name;
        $location.path( '/' + name );
      };
    }
  }

})();

(function() {
  'use strict';

  angular
    .module('webFront')
    .directive('daterangepicker', daterangepicker);

  /** @ngInject */
  function daterangepicker() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/daterangepicker/daterangepicker.html',
      controller: DaterangepickerController,
      controllerAs: 'vm',
      bindToController: true,
      link:Link,
      require: "ngModel"
    };

    return directive;

    /** @ngInject */
    function DaterangepickerController() {
      var vm = this;
    }

    function Link( scope, element, attrs, ngModel ){
      scope.start = moment().subtract(29, 'days').format( 'YYYY-MM-DD' );
      scope.end = moment().format( 'YYYY-MM-DD' );
      ngModel.$setViewValue( { start: scope.start, end: scope.end } );

      $(element).daterangepicker({
        locale: {
          format: 'YYYY-MM-DD',
          customRangeLabel: '自定义'
        },
        dateLimit: {
          days: 500
        },
        startDate: moment().subtract(29, 'days'),
        endDate: moment(),
        maxDate: moment(),
        ranges: {
          '今日': [moment(), moment()],
          '昨天': [moment().subtract(1, 'days'), moment().subtract(1, 'days')],
          '最近7天': [moment().subtract(6, 'days'), moment()],
          '最近30天': [moment().subtract(29, 'days'), moment()]
          //'本月': [moment().startOf('month'), moment().endOf('month')],
          //'上个月': [moment().subtract(1, 'month').startOf('month'), moment().subtract(1, 'month').endOf('month')]
        }
      }, function( start, end ){
        scope.start = start.format('YYYY-MM-DD');
        scope.end = end.format('YYYY-MM-DD');
        ngModel.$setViewValue( { start: start.format('YYYY-MM-DD'), end: end.format('YYYY-MM-DD') } );
      });
    }


  }

})();

(function() {
  'use strict';

  angular
    .module('webFront')
    .factory('githubContributor', githubContributor);

  /** @ngInject */
  function githubContributor($log, $http) {
    var apiHost = 'https://api.github.com/repos/Swiip/generator-gulp-angular';

    var service = {
      apiHost: apiHost,
      getContributors: getContributors
    };

    return service;

    function getContributors(limit) {
      if (!limit) {
        limit = 30;
      }

      return $http.get(apiHost + '/contributors?per_page=' + limit)
        .then(getContributorsComplete)
        .catch(getContributorsFailed);

      function getContributorsComplete(response) {
        return response.data;
      }

      function getContributorsFailed(error) {
        $log.error('XHR Failed for getContributors.\n' + angular.toJson(error.data, true));
      }
    }
  }
})();

angular.module('webFront').factory('StockService', ['$http', function ($http) {
  return {
    get: function (params, cb) {
      var url = '/api_v1.1/stocks';
      return $http.get( url,  { params: params }).success(function (data) {
        cb( data );
      });
    }
  }
}]);

